<template>
    <div class="hello">
        <p v-if="onlineStatus">This part will be visible only if user is online</p>
        <p v-if="!onlineStatus">This part will be visible only if user is offline</p>
        {{onlineStatusMsg}}
        <el-button type="primary" @click="openUserDialog">SEND USER DATA</el-button>
        <el-dialog title="Outer Dialog" :visible.sync="outerVisible">
            <el-tag type="success" v-if="onlineStatus">You seem to be online.</el-tag>
            <el-tag
                type="danger"
                v-if="!onlineStatus"
            >You seem to be offline. Your data will be save successfully Connect to send</el-tag>
            <el-form ref="form" :model="form" :rules="rules">
                <el-form-item label="Username" prop="username">
                    <el-input v-model="form.username" placeholder="Username"></el-input>
                </el-form-item>
                <p>Username is: {{ form.username }}</p>
                <el-form-item label="Age" prop="age">
                    <el-input v-model="form.age" placeholder="Age"></el-input>
                </el-form-item>
                <p>Age is: {{ form.age }}</p>
                <el-form-item label="Sex" prop="sex">
                    <el-radio-group v-model="form.sex">
                        <el-radio label="Male"></el-radio>
                        <el-radio label="Female"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-button type="primary" @click="sendUserData('form')">SEND</el-button>
            </el-form>
        </el-dialog>
        <el-row>
            <span>
                We're now using
                <a href="https://onesignal.com/">OneSignal</a> for notifications such as Event start/end alert.
            </span>
            <el-button @click="handleSubscribe">Subscribe</el-button>
        </el-row>
    </div>
</template>

<script>
import axios from 'axios';
import ServiceWorker from './ServiceWorker.vue';

export default {
  name: 'HelloWorld',
  components: {
    ServiceWorker
  },
  data () {
    return {
      onlineStatus: null,
      photo: null,
      dataReadyToSent: false,
      onlineStatusMsg: '',
      form: {
        age: '' || window.localStorage.getItem('age'),
        username: '' || window.localStorage.getItem('username'),
        sex: '' || window.localStorage.getItem('sex')
      },
      rules: {
        age: [
          {
            required: true,
            message: 'Please input your Age',
            trigger: 'blur'
          }
        ],
        username: [
          {
            required: true,
            message: 'Please input your Name',
            trigger: 'change'
          }
        ],
        sex: [
          {
            required: true,
            message: 'Please choice your sex',
            trigger: 'change'
          }
        ]
      },
      outerVisible: false
    };
  },
  props: {
    msg: String
  },
  async mounted () {
    this.localStorageSupport();
    window.addEventListener('load', () => {
      this.checkWifi();
      window.addEventListener('sync', this.backgroundSync);
      window.addEventListener('online', this.checkWifi);
      window.addEventListener('offline', this.checkWifi);
    });
  },
  beforeDestroy: function () {
    window.removeEventListener('sync', this.backgroundSync);
    window.removeEventListener('online', this.checkWifi);
    window.removeEventListener('offline', this.checkWifi);
  },
  methods: {
    loadCameraIds (device) {
      this.deviceId = device[0].deviceId;
    },
    backgroundSync (event) {
      if (event.tag == 'myFirstSync') {
        if (this.onlineStatus && this.dataReadyToSent) {
          this.makeAjaxCall();
        }
      }
    },
    checkWifi () {
      this.onlineStatus = !!navigator.onLine;
      this.onlineStatusMsg = navigator.onLine
        ? 'My network is fine'
        : 'I am offline';
      if (this.onlineStatus && this.dataReadyToSent) {
        this.makeAjaxCall();
      }
      navigator.onLine
        ? this.$message({
          message: 'My network is fine',
          type: 'success'
        })
        : this.$message.error(
          'You seem to be offline. Connect to send user data'
        );
    },
    sendUserData (formName) {
      if (window.localStorage) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            return this.onlineStatus
              ? this.makeAjaxCall()
              : this.saveToLocalStorage();
          }
          return false;
        });
      }
    },
    openUserDialog () {
      this.outerVisible = true;
    },
    makeAjaxCall () {
      const data = {
        username: this.username || window.localStorage.getItem('username'),
        age: this.age || window.localStorage.getItem('age'),
        sex: this.sex || window.localStorage.getItem('sex')
      };
      axios
        .post('https://www.mocky.io/v2/5c6d101d3700001119fa31a8', {
          body: data
        })
        .then(response => {
          this.$message({
            message: 'Your data was successfully sent',
            type: 'success'
          });
          this.clearStorage();
          this.clearForm();
          console.log(response);
        })
        .catch(e => {
          this.errors.push(e);
        });
    },
    saveToLocalStorage () {
      window.localStorage.setItem('username', this.form.username);
      window.localStorage.setItem('age', this.form.age);
      window.localStorage.setItem('sex', this.form.sex);
      this.dataReadyToSent = true;
      this.$message({
        message:
          'You seem to be offline, Your data save successfully Connect to send',
        type: 'warning'
      });
      this.clearForm();
      navigator.serviceWorker.ready.then(registration => {
        registration.sync.register('myFirstSync');
      });
    },
    localStorageSupport () {
      const localStorage = !!window.localStorage;
      if (!localStorage) {
        this.$message.error('Local Storage not supported in browser');
      }
      return localStorage;
    },
    clearStorage () {
      window.localStorage.removeItem('username');
      window.localStorage.removeItem('age');
      window.localStorage.removeItem('sex');
      this.dataReadyToSent = false;
    },
    clearForm () {
      this.form.username = '';
      this.form.age = '';
      this.form.sex = '';
      this.outerVisible = false;
    },
    handleSubscribe () {
      const OneSignal = window.OneSignal || [];
      OneSignal.registerForPushNotifications();
    },
    takePhoto () {}
  }
};
</script>

